@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    padding: 0;
    margin: 0;
    list-style: none;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
  }

  h1, h2, h3, h4, h5, h6, p, i, b, strong, a, li, div {
    color: #222;
  }

  .number:active {
    width: 75px;
    height: 65px;
    -webkit-box-shadow: 2px 0px 20px -2px rgba(245,224,117,1);
    -moz-box-shadow: 2px 0px 20px -2px rgba(245,224,117,1);
    box-shadow: 2px 0px 20px -2px rgba(245,224,117,1);
  }
  .nextBtn {
    clip-path: polygon(94% 0, 100% 50%, 94% 100%, 0% 100%, 0 52%, 0% 0%);
    background-color: #f09952;
  }
  .backBtn {
    clip-path: polygon(100% 0, 100% 50%, 100% 100%, 6% 100%, 0 52%, 6% 0);
    background-color: #aaa;
  }